<template>
  <img :src="imgUrl"
   :alt="checkAvailable()? sticker.name : 'Épuisé'" v-on:click.self="select($event)" :coords="sticker.coords" :class="{notavailable : !checkAvailable()}" width="300px" height="300px"/>
</template>

<script>
export default {
  props: [
    'sticker'
  ],
  data () {
    return {
      imgUrl: 'static/img/' + this.sticker.sticker + '.png'
    }
  },
  methods: {
    select (event) {
      if (this.checkAvailable()) {
        this.$emit('select', this.sticker)
      }
    },
    checkAvailable () {
      return this.sticker.quantity > 0
    },
    imageLoadOnError () {
      this.imgUrl = 'https://octodex.github.com/images/' + this.sticker.sticker.toLowerCase() + '.jpg'
    }
  }
}
</script>

<style scoped>
  .notavailable{
    cursor: no-drop;
  }
</style>



